<template>
    <div id="app">
        <Main v-if="stage === 'main'" />
        <transition name="init-screen">
            <InitScreen v-if="stage === 'init'" @init-screen-end="stage = 'main'"/>
        </transition>
    </div>
</template>

<script>
    import InitScreen from './components/InitScreen.vue'
    import Main from "./components/Main";

    export default {
        name: 'App',
        components: {
            Main,
            InitScreen
        },
        data: function () {
            return {
                stage: 'init'
            }
        }
    }
</script>

<style>
    .init-screen-leave-active {
        transition: opacity 1s ease-out;
    }
    .init-screen-leave-to {
        opacity: 0;
    }
</style>
